import React from 'react' ;

const menus = [
  {
    label: 'Home',
    path: '/',
  },
  {
    label: 'Problem',
    path: '/problem',
  },
  {
    label: 'Solution',
    path: '/solution',
  },
  {
    label: 'NFT',
    path: '/NFT',
  },
  // {
  //   label: 'Tokenomics',
  //   path: '/tokenomics',
  // },
  {
    label: 'Team',
    path: '/team',
  },
  {
    label: 'Roadmap',
    path: '/roadmap',
  },
  {
    label: 'Contact',
    path: '/contact',
  },
];
function LeftAsider(props: any) {

  const roundShow = (label: any) => {
    console.log('label', label)
    console.log('props', props.active)
    if (label === props.active) {
      return <div className=' w-2 h-2 rounded-full bg-[#06D057] ring-2 ring-[#06D057] ring-opacity-20'></div>
    } else {
      return <div className=' w-1 h-1 rounded-full bg-[#777777]'></div>
    }
  }
  return <div className=' w-20 flex justify-center flex-col items-center md:hidden'>
    <div className=" w-[1px] h-full border border-dashed border-white border-opacity-20 flex items-center relative">
      <div className=' absolute -left-1 space-y-3 flex justify-center items-center flex-col'>
        {
          menus.map((item, index) => {
            return (
              <div key={index}>
                {roundShow(item.label)}
              </div>
            )
          })
        }
      </div>
    </div>
</div>;
}

export default LeftAsider;
