import React, { useState } from 'react';
import { NavLink } from 'react-router-dom';
import Logo from '@/assets/images/logo.png';
import {  Drawer } from 'antd';
import { MenuOutlined } from '@ant-design/icons';
import { useNavigate ,useLocation} from 'react-router-dom';

import './styles.less';

const menus = [
  {
    label: 'Home',
    path: '/',
  },
  {
    label: 'Problem',
    path: '/problem',
  },
  {
    label: 'Solution',
    path: '/solution',
  },
  {
    label: 'NFT',
    path: '/NFT',
  },
  // {
  //   label: 'Tokenomics',
  //   path: '/tokenomics',
  // },
  {
    label: 'Team',
    path: '/team',
  },
  {
    label: 'Roadmap',
    path: '/roadmap',
  },
  {
    label: 'Contact',
    path: '/contact',
  },
];

function Header() {
  const content = (
    <>
      {menus.map((item) => (
        <div className="p-5 md:p-3 cursor-pointer font-semibold text-base" key={item.label}>
          <NavLink
            end
            style={({ isActive }) => ({
              color: isActive ? '#1FF6B6' : '#FFFFFF',
              fontWeight: isActive ? '700' : '400',
            })}
            to={item.path}
          >
            {item.label}
          </NavLink>
        </div>
      ))}
    </>
  );

  const [open, setOpen] = useState(false);

  const navigate = useNavigate();

  const location = useLocation();

  return (
    <div className="w-full h-16 bg-[#030001] bg-opacity-30 px-11 fixed z-50 md:px-5 md:py-4 md:h-auto">
      <div className="flex justify-between items-center shadow-sm">
        <div className="w-auto flex justify-center items-center">
          <img src={Logo} alt="" className=" w-36" />
        </div>
        <div className="flex items-center">
          <div className="flex justify-between items-center md:hidden">
            {/* {menus.map((item) => (
              <div className="p-5 md:px-3 cursor-pointer text-base font-medium text-white" key={item.label}>
                <NavLink to={item.path} className="text-white hover:text-[#1FF6B6]">{item.label}</NavLink>
              </div>
            ))} */}
            {content}
          </div>
          <div
            className="md:block lg:hidden xl:hidden 2xl:hidden"
            onClick={() => {
              setOpen(true);
            }}
          >
            <MenuOutlined style={{ color: '#fff', fontSize: '20px' }} />
          </div>
        </div>
      </div>

      <Drawer
        className="drawer__right"
        key={open?1:0}
        placement="right"
        onClose={() => setOpen(false)}
        open={open}
        width={'100%'}
        closable={false}
      >
        <div
          className="absolute right-[15px] top-[15px]"
          onClick={() => {
            setOpen(false);
          }}
        >
          <img src="/static/images/close.svg" alt="" />
        </div>
        <div className="mobileHeader__nav w-full px-[20px] relative">
          {menus.map((item) => {
            return (
              <div
                onClick={() => {
                  setOpen(false);
                  navigate(item.path);
                }}
                key={item.path}
                className={`
                w-full px-5 py-3 transition-all cursor-pointer mt-[20px]
                rounded-md color-[#fff]
                ${location.pathname === item.path && ' text-[#1FF6B6]'}
                `}
              >
                {item.label}
              </div>
            );
          })}
        </div>

        <div
          className="absolute w-full bottom-[25px] flex justify-center"
          onClick={() => {
            setOpen(false);
          }}
        >
          <img src="/static/images/twitter.svg" className="mr-[30px]" alt="" />
          <img src="/static/images/tg.svg" alt="" />
        </div>
      </Drawer>
    </div>
  );
}

export default Header;
