import React, { Suspense } from 'react';
import { Route, Routes as ReactRoutes } from 'react-router-dom';
import DefaultLayout from '@/components/Layout/Default';
import NProgress from '@/components/UI/NProgress';

import Collections from '@/views/Home';

const Problem = React.lazy(() => import('@/views/Problem'));
const Solution = React.lazy(() => import('@/views/Solution'));
const SolutionSec = React.lazy(() => import('@/views/SolutionSec'));
const SolutionThird = React.lazy(() => import('@/views/SolutionThird'));
const NFT = React.lazy(() => import('@/views/NFT'));
const Tokenomics = React.lazy(() => import('@/views/Tokenomics'));
const Team = React.lazy(() => import('@/views/Team'));
const Roadmap = React.lazy(() => import('@/views/Roadmap'));
const Contact = React.lazy(() => import('@/views/Contact'));

const Routes = () => {
  return (
    <Suspense fallback={<NProgress />}>
      <ReactRoutes>
        <Route path="/" element={<DefaultLayout />}>
          <Route path="/" key="Collections" element={<Collections />} />
          <Route path="/problem" key="Problem" element={<Problem />} />
          <Route path="/solution" key="Solution" element={<Solution />} />
          <Route path="/NFT" key="NFT" element={<NFT />} />
          {/* <Route path="/tokenomics" key="Tokenomics" element={<Tokenomics />} /> */}
          <Route path="/team" key="Team" element={<Team />} />
          <Route path="/roadmap" key="Roadmap" element={<Roadmap />} />
          <Route path="/contact" key="Contact" element={<Contact />} />
        </Route>
      </ReactRoutes>
    </Suspense>
  );
};

export default Routes;
