
import React from 'react';
import classNames from 'classnames';

export const Button: React.FC<{
  className?: string;
  onClick?: (e: any) => void;
  name?: string;
}> = ({className, name, onClick}) => {
  return <button className={classNames(' h-14', 'w-full', 'flex-shrink-0', 'border-none', 'text-white', 'text-base', 'rounded-lg', 'focus:outline-none', className)} onClick={(e) => {
      onClick?.(e);
  }}>{name}</button>;
};
